.registro-tabla {
  width: 100%;
  border-collapse: collapse;
}

.registro-tabla th,
.registro-tabla td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center; /* Centrar el contenido */
}

.registro-tabla th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.registro-tabla tr:nth-child(even) {
  background-color: #f9f9f9;
}

.registro-tabla tr:hover {
  background-color: #ddd;
}